<template>
  <section class="row">
    <template v-for="(item, index) in orderStatus" :key="index">
      <div class="card border-dark col paperDarkBg">
        <div class="card-body text-center">
          <router-link
            :to="item.path"
            class="text-decoration-none text-dark p-2 fw-bold h5"
            @click="random"
          >
            {{ $t(item.name) }}
            <Circle1
              v-if="CircleNum === 2"
              :class="{
                'd-none':
                  this.$route.path.split('/')[2] !== item.path.split('/')[2],
              }"
            />
            <Circle2
              v-else-if="CircleNum === 1"
              :class="{
                'd-none':
                  this.$route.path.split('/')[2] !== item.path.split('/')[2],
              }"
            />
            <Circle3
              v-else
              :class="{
                'd-none':
                  this.$route.path.split('/')[2] !== item.path.split('/')[2],
              }"
            />
          </router-link>
        </div>
      </div>
    </template>
  </section>
  <section class="row">
    <div class="card border-dark">
      <div class="card-body">
        <router-view></router-view>
      </div>
    </div>
  </section>
</template>

<script>
import Circle1 from '@/components/helpers/focus/CircleOne.vue';
import Circle2 from '@/components/helpers/focus/CircleTwo.vue';
import Circle3 from '@/components/helpers/focus/CircleThree.vue';

export default {
  data() {
    return {
      CircleNum: 0,
      orderStatus: [
        { name: 'orderOptions.pre-payment', path: '/order/prepayment' },
        { name: 'orderOptions.paymentCompleted', path: '/order/completPay' },
        { name: 'orderOptions.orderCompleted', path: '/order/completOrder' },
        { name: 'orderOptions.diningOut', path: '/order/dining' },
        { name: 'orderOptions.invalid', path: '/order/invalid' },
      ],
    };
  },
  components: {
    Circle1,
    Circle2,
    Circle3,
  },
  methods: {
    random() {
      this.CircleNum = Math.floor(Math.random() * 3);
    },
  },
};
</script>
